.container__heading {
  font-size: 48px;
  font-weight: 700;
  line-height: 48px;
  text-align: center;
  display: block;
  transition: font-size $transition-time, line-height $transition-time;

  &::after {
    content: "";
    display: block;
    width: 20px;
    height: 13px;
    margin: 8px auto 0;
    background-image: url("../images/crown.svg");
    background-size: cover;
    background-position: center;
  }
}

.photos {
  display: flex;
  justify-content: space-between;

  &__link {
    color: $blue;
  }
}

.photo__img {
  height: 256px;
  width: calc(50% - 10px);
  border-radius: 8px;
  object-fit: cover;
  object-position: center;
  transition: 1s;

  &-small {
    width: calc(33% - 10px);
  }

  &-big {
    width: calc(67% - 10px);
  }

  &:hover {
    transform: scale(1.2);
  }
}

.gallery {
  &__button {
    width: 188px;
    height: 56px;
    color: $blue;
    border: 2px solid $blue;
    border-radius: 8px;
    font-weight: bold;
    font-size: 16px;
    line-height: 56px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto 152px;
  
    &:hover {
      box-shadow: 0 13px 27px rgba(0, 83, 181, 0.46);
      cursor: pointer;
    }
  }

  &__description {
    display: block;
    color: $dark-grey;
    font-weight: bold;
    font-size: 16px;
    line-height: 27px;
    margin: 16px auto 24px;
  }
}

@media screen and (max-width: 767px) {

  .photo__img {
    height: 132px;
    width: calc(50% - 10px);

    &-small {
      width: calc(50% - 10px);
    }

    &-big {
      width: calc(50% - 10px);
    }
  }
}
